import React from "react";
import PropTypes from "prop-types";
import { graphql } from "gatsby";

import Layout from "../components/Layout";
import HeadComponent from "../components/blocks/header.jsx";
import CallpapperComponent from "../components/blocks/callpappers";
import AboutComponent from "../components/blocks/about";
import TicketsComponent from "../components/blocks/tickets";
import SpeakersComponent from "../components/blocks/speakers";
import WorkshopComponent from "../components/blocks/workshop";
import Sponsorship from "../components/blocks/sponsorship";
import Location from "../components/blocks/location";
import HotelsPartners from "../components/blocks/hotels-partners";
import ProgramCommitee from "../components/blocks/program-commitee";
import Footer from "../components/blocks/footer";
import FixedSocial from "../components/blocks/fixed-social";
import Schedule from "../components/blocks/schedule";
import InfoSlider from "../components/blocks/info-slider";
import Video from "../components/blocks/video";

export const IndexPageTemplate = ({
  head_data,
  callpappers_data,
  about_data,
  tickets_data,
  speakers_data,
  location_data,
  info_slider_data
}) => (
  <div>
    <HeadComponent data={head_data} />
    {/*<AboutComponent data={about_data} />*/}
    {/*<InfoSlider data={info_slider_data} />*/}
    {/*<TicketsComponent data={tickets_data} />*/}
    {/*<SpeakersComponent data={speakers_data} />*/}
    {/*<Schedule />*/}
    {/*<WorkshopComponent />*/}
    {/*<Video />*/}
    {/*<Sponsorship />*/}
    {/*<HotelsPartners />*/}
    {/*<Location data={location_data} />*/}
    {/*<ProgramCommitee />*/}
    <Footer />
    <FixedSocial />
  </div>
);

IndexPageTemplate.propTypes = {
  head_data: PropTypes.object,
  info_slider_data: PropTypes.object,
  callpappers_data: PropTypes.object,
  about_data: PropTypes.object,
  tickets_data: PropTypes.object,
  speakers_data: PropTypes.object,
    location_data: PropTypes.object
};

const IndexPage = ({ data }) => {
  const { frontmatter } = data.markdownRemark;
    console.log('frontmatter', frontmatter);
  return (
    <Layout>
      <IndexPageTemplate
        head_data={frontmatter.head_data}
        info_slider_data={frontmatter.info_slider_data}
        callpappers_data={frontmatter.callpappers_data}
        about_data={frontmatter.about_data}
        tickets_data={frontmatter.tickets_data}
        speakers_data={frontmatter.speakers_data}
        location_data={frontmatter.location_data}
      />
    </Layout>
  );
};

IndexPage.propTypes = {
  data: PropTypes.shape({
    allMarkdownRemark: PropTypes.shape({
      frontmatter: PropTypes.object
    }),
    markdownRemark: PropTypes.shape({
      frontmatter: PropTypes.object
    })
  })
};

export default IndexPage;

export const pageQuery = graphql`
  query IndexPageTemplate {
    markdownRemark(frontmatter: { templateKey: { eq: "index-page" } }) {
      frontmatter {
        head_data {
          title
          image {
            childImageSharp {
              fluid(maxWidth: 1200, quality: 100) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
        callpappers_data {
          title
          description
          btn__text
          btn__link
        }
        about_data {
          subtitle
          benefits {
            title
            subtitle
          }
          sm {
            link
            class
          }
        }
        tickets_data {
          title
          subtitle
          description
          tickets {
            name
            subname
            desc
            price
            promo
            action
            color
            link
          }
        }
        speakers_data {
          title
          speakers {
            image {
              childImageSharp {
                fluid(maxWidth: 350, quality: 100) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
            name
            positions {
              position
              company
            }
            place {
              country
              city
            }
            reports {
              title
              description
              lang
            }
            socials {
              link
              icon
            }
          }
        }
        
        location_data {
          image {
            childImageSharp {
              fluid(maxWidth: 1200, quality: 100) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
        
        info_slider_data {
          title
          firstSlide {
            title
          }
          secondSlide {
            title
          }
          thirdSlide {
            title
          }
        }
      }
    }
  }
`;
